	module.exports = {"data":{"post":{"html":"<p>I've just been informed that the German technology magazine <a href=\"http://www.heise.de/ct/\">c/t</a> has without our permission published my and Christopher's picture in their latest issue (05/2007, p. 70), in the screen shot of a feed reader. So who do I sue? The <a href=\"http://www.schockwellenreiter.de/2007/02/09.html#harzigenGlhstrumpf\">original post</a> comes from Jörg Kantel's <a href=\"http://www.schockwellenreiter.de/\">Der Schockwellenreiter</a>, but he works for a public institution, so there are no deep pockets there. But <a href=\"http://vowe.net/\">Volker Weber</a> writes for c/t, maybe I can score one of his Blackberries....</p>","fields":{"title":"Who Do I Sue?","date":"28 Feb 2007","url":"https://papascott.de/2007-02-28-who-do-i-sue/"}}},"pathContext":{"slug":"/2007-02-28-who-do-i-sue/","prev":{"fields":{"title":"Organic Foods","date":"28 Feb 2007","slug":"/2007-02-28-organic-foods/"}},"next":{"fields":{"title":"Mickey Mouse Outfit","date":"04 Mar 2007","slug":"/2007-03-04-mickey-mouse-outfit/"}}}}

